import {Application} from "../application";
const settings = require('electron-settings');
import electron = require("electron");
import {BrowserWindow} from 'electron';

export class PromptPassword {

    // Prompt a window to request the master password for multi account
    public static run(): Electron.BrowserWindow {

        let promptWindow: Electron.BrowserWindow;
        
        promptWindow = new BrowserWindow({
            width: 400,
            height: 200,
            title: "DofusTouchNE",
            show: false,
            center: true,
            movable: true,
            alwaysOnTop: true,
            resizable: false,
            //frame: false,
            //transparent: true,
        });
        
        //promptWindow.loadURL(`file://${Application.appPath}/out/electron/windows/prompt-password/prompt-password.html`);
        promptWindow.loadURL(`file://${Application.appPath}/out/browser/index.html#/prompt-password`);
        
        promptWindow.show();
        promptWindow.setMenu(null);

        //promptWindow.loadURL(`file://${Application.appPath}/out/browser/index.html#/changelog`);


        promptWindow.on('close', (event:any) => {
            promptWindow = null;
        });

        return promptWindow;
    }

    // Display "wrong password" tip
    public static displayWrongPassword(promptWindow: Electron.BrowserWindow) {
        promptWindow.webContents.send("wrong-password");
    }
}